// initial state
// shape: [{ id, quantity }]
const state = {
    userInfo: {},
    token: '',
  }
  
  // getters
  const getters = {
    token:state=>state.token,
    userInfo:state=>state.userInfo,
  }
  
  // actions
  const actions = {
    
  }
  
  // mutations
  const mutations = {
    getUserinfo (state, { info }) {
        state.userInfo=info;
    },
    getToken (state,{token}){
        state.token=token;
    },
  }
  
  export default {
    state,
    getters,
    actions,
    mutations
  }


