<template>
  <div class="login">
        <div class="enter" v-if="!oldFlag">
            <span class="name">昵称</span>
            <input type="text"  v-model="username" >
            <div class="enter-button">
                <mt-button plain type="primary" @click="jump">登入</mt-button>
            </div>
        </div>
        <div class="enter"  v-else>
            <h2>{{username}},欢迎回来</h2>
            <p><mt-spinner type="triple-bounce"></mt-spinner></p>
        </div>
        <div class="bulr"></div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  name: 'login',
  data () {
    return {
        username:'',
        password:'',
        oldFlag:false,
    }
  },
  methods:{
    jump(){
        socket.removeAllListeners() 
        //将登录名字发送到服务器
        socket.emit('login',this.username)
        socket.on('loginSuccess',data=>{
            sessionStorage.setItem('username',this.username);
            this.$router.push('/index')
            Toast('登录成功')
        })
        socket.on('nickExisted',data=>{
            Toast('当前用户名已被注册');
        })
    }
  },
  created(){
       
  },
  mounted(){
   
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    @import 'login.scss';
</style>



