import Vue from 'vue'
import Router from 'vue-router'
import Mine from './nestedRoutes/mine.js'
import Share from './nestedRoutes/share.js'
import MicroLesson from './nestedRoutes/microLesson.js'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path:'/',
      redirect: '/formula',
    },
    ...Mine.routes,
    ...Share.routes,
    ...MicroLesson.routes,
  ]
})



