<template>
    <div class="tabbars">
        <mt-tabbar v-model="changer">
            <mt-tab-item id="formula">
                <img v-if="changer=='formula'" slot="icon" src="/static/image/tabbar/tab_lesson_choose.png">
                <img v-else slot="icon" src="/static/image/tabbar/tab_lesson.png">

                <span v-if="changer=='formula'" style="color:#ff5c67">微课</span>
                <span v-else  >微课</span>
                
            </mt-tab-item>
            <mt-tab-item id="mine">
                <img v-if="changer=='mine'" slot="icon" src="/static/image/tabbar/tab_center_checked.png">
                <img v-else slot="icon" src="/static/image/tabbar/tab_center_unchecked.png">
                
                <span v-if="changer=='mine'" style="color:#ff5c67">我的</span>
                <span v-else  >我的</span>
            </mt-tab-item>
        </mt-tabbar>
    </div>
</template>
<script>
export default {
    name: 'Tabbar',
    props:{
        senar:Object,
    },
    data () {
        return {
            changer:'',
        }
    },
    watch:{
        changer(val){
            
            this.$router.push({
                name:val,
            })
        },
    },
    mounted(){
        this.changer=this.senar.name;
    }
}
</script>
<style lang="scss">
    @import '../assets/style/global_style.scss';
    .tabbars {
        .mint-tab-item-label {
            font-size: 12px !important;
            margin-bottom: 3px !important;
            color:#7B7C7C !important;
        }
        .mint-tab-item-icon {
            width: 31px !important;
            height: 31px !important;
            margin: 3px auto 0 !important;
        }
        .mint-tab-item {
            padding: 0px 0 3px 0 !important;
        }
        .tabbars {
            position: fixed;
            bottom: 0px;
            width: 100%;
        }
        .mint-tabbar > .mint-tab-item.is-selected {
            background-color: #fff !important;
            color: #FF5C67 !important;
        }
        .mint-tabbar {
            background-image: -webkit-linear-gradient(top, #d9d9d9, #d9d9d9 50%, transparent 50%);
            background-image: linear-gradient(180deg, #d9d9d9, #d9d9d9 50%, transparent 50%);
            background-size: 100% 0px;
            background-repeat: no-repeat;
            background-position: top left;
            /*position: relative;*/
            background-color: #fff !important;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            right: 0;
            bottom: 0;
            left: 0;
            position: fixed;
            text-align: center;
            border-top: 1px solid #f3f3f3 !important;
        }
    }
    
</style>





