import Index from '@/components/microLesson/index/Index.vue'
import Login from '@/components/microLesson/login/Login.vue'
import ColumnDetail from '@/components/microLesson/detail/column/ColumnDetail.vue'
import CourseDetail from '@/components/microLesson/detail/course/CourseDetail.vue'
import Formula from '@/components/microLesson/formula/Formula.vue'

const routes=[
    {
        path: '/index',
        name: 'index',
        component: Index
    },
    {
        path: '/login',
        name: 'login',
        component: Login
    },
    {
        path: '/columnDetail',
        name: 'columnDetail',
        component: ColumnDetail
    },
    {
        path: '/courseDetail',
        name: 'courseDetail',
        component: CourseDetail
    },
    {
        path: '/formula',
        name: 'formula',
        component: Formula
    },
    
]
export default {routes}


