<template>
  <div class="chatroom">
        <div class="room" id="room">
          <div id="content">
            <div class="content-box" v-for="item in messageList" :key="item.id">
              <p class='content-time' v-if="item.showTime">{{item.time}}</p>
              <span :style="`color:${item.color}`">{{item.author}}</span>
              <p class="content-box-message">{{item.message}}</p>
            </div>
          </div>
        </div>
        <div class="entry">
            <span id="status">Connecting...</span>
            <input type="text" id="input" v-model="message"/>
            <span class="post" @click="postMessage">
               发送
            </span>
        </div>
  </div>
</template>

<script>
import $ from 'jquery';
 let baseUrl = '',
    connection;
  if (location.hostname == 'localhost') {
    baseUrl = 'http://localhost';
    // baseUrl = '119.23.57.251:3000'
  } else {
    baseUrl = '119.23.57.251:3000'
  }
export default {
  name: 'index',
  data () {
    return {
        username:'',
        password:'',
        message:'',
        oldTime:'',
        momentID:'',
        messageList:[],
    }
  },
  methods:{
    postMessage(){
      // connection.send(this.message);
      this.addMessage(this.username,this.message,'green',new Date())
       socket.emit('postMsg',this.message,'green')
      this.message='';
    },
    addMessage(author, message, color, dt) {
        let time=(dt).valueOf(),
            difference=time-this.oldTime,
            mesObj={};
        if(difference/1000>=60){
          mesObj.time= dt.getHours() + ':'
             + (dt.getMinutes()<10?('0'+ dt.getMinutes()):dt.getMinutes())
          mesObj.showTime=true;
        }else{
          mesObj.showTime=false;
        }
        mesObj.color=color;
        mesObj.message=message;
        mesObj.author=author;
        this.oldTime=time;
        this.messageList.push(mesObj);
    },
    websocketLoad(){
      var status = $('#status');
      var input = $('#input');
      var self=this;
      var myColor = false;
      var myName = self.username;
      connection = new WebSocket('ws:' + baseUrl);
      connection.onopen = function () {
          connection.send(self.username);
      };
      connection.onmessage = function (message) {
        var json = JSON.parse(message.data);
          if (json.type === 'color') { // first response from the server with user's color
              myColor = json.data;
              status.text(myName + ': ').css('color', myColor);
          }else if (json.type === 'message') { // it's a single message
              self.addMessage(json.data.author, json.data.text,
                        json.data.color, new Date(json.data.time));
          } 
      };
      input.keydown(function(e) {
          if (e.keyCode === 13) {
              connection.send(self.message);
             self.message='';
          }
      });
    },
    socketLoad(){
      // socket.removeAllListeners();
      var status = $('#status');
      var input = $('#input');
      status.text(this.username + ': ').css('color', 'green');
      input.keydown(e=> {
          if (e.keyCode === 13) {
              this.postMessage();
          }
      });
      socket.on('newMsg',(name,msg,color)=>{
        this.addMessage(name,msg,color,new Date())
      })

    },

  },
  created(){
        this.username=sessionStorage.getItem('username');
  },
  mounted(){
    this.socketLoad();
  },
  destroyed(){
    // connection.close();
    socket.emit('disconnect')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
    @import 'index.scss';
</style>



