<template>
  <div class="formula">
        <!-- <mt-search v-model="keyword"></mt-search> -->
        <div class="formula-navbar">
          <mt-navbar v-model="selected">
            <mt-tab-item id="1">推荐</mt-tab-item>
            <mt-tab-item id="2">育儿</mt-tab-item>
            <mt-tab-item id="3">金融理财</mt-tab-item>
            <mt-tab-item id="4">互联网</mt-tab-item>
            <mt-tab-item id="5">培训</mt-tab-item>
            <mt-tab-item id="6">心灵</mt-tab-item>
            <mt-tab-item id="7">爱情</mt-tab-item>
            <mt-tab-item id="8">辣妈</mt-tab-item>
            <mt-tab-item id="9">科技</mt-tab-item>
          </mt-navbar>
          <div class="formula-navbar-add">
            <i class="fa fa-plus"></i>
          </div>
        </div>
        <mt-swipe :auto="4000">
          <mt-swipe-item v-for="img in swiperList"  :key="img.id">
            <img :src="img.src" alt="">
          </mt-swipe-item>
        </mt-swipe>
        <div class="formula-list">
          <ul>
            <li v-for="cls in classList " :key="cls.id" @click="jump('detail',cls)">
              <div class="formula-list-img" :style="`background-image:url(${cls.imgUrl})`"></div>
              <div class="formula-list-content">
                <p>{{cls.name}}</p>
                <div v-if="cls.immediate" class="formula-list-content-running">
                  <img src="/static/image/formula/voice.png" alt="">
                  <span style="color:#1aad19">正在直播</span>
                </div>
                <div class="formula-list-content-bottom">
                    <span v-if="cls.Single" class="bottom-period">已更新{{cls.singleNum}}期</span>
                    <span v-else class="bottom-period">{{cls.startTime}} </span>
                    <span class="bottom-person"><span style="color:#666">{{cls.person>10000?`${cls.person/10000}万`:cls.person}}</span>人在学</span>
                    <span class="bottom-price" :style="cls.price==0?'font-size:12px':''">{{cls.price==0?'限时免费':`￥${cls.price}`}}</span>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <tabbar :senar="senars"></tabbar>
  </div>
</template>

<script>
import tabbar from '@/global_components/Tabar.vue';
import Interface from '../../../util/interface.js';
export default {
  name: 'formula',
  components:{
    tabbar
  },
  data () {
    return {
      //关键词
      keyword:"",
      //navbar的选择状态
      selected:'1',
      //menu选项
      senars:{
        name:'formula',
      },
      swiperList:[{
        id:1,
        src:'https://static-1253442168.image.myqcloud.com/banner/None_d41d8cd98f00b204e9800998ecf8427e_uWncq6h.jpg-bannerhd768',
        url:'',
      },{
        id:2,
        src:'https://static-1253442168.image.myqcloud.com/banner/None_d41d8cd98f00b204e9800998ecf8427e_IxrERw4.jpg-bannerhd768',
        url:'',
      },{
        id:3,
        src:'https://static-1253442168.image.myqcloud.com/banner/None_d41d8cd98f00b204e9800998ecf8427e_VAa7H7a.jpg-bannerhd768',
        url:'',
      },],
      classList:[{
          id:1,
          imgUrl:'https://static-1253442168.image.myqcloud.com/upload/5674023_aaa8c754d8357ab482033cd2d7fd57cd.jpg-lecturecover85hd',//微课图片
          name:'关于瑞文光速QA实战教程,关于瑞文光速QA实战教程关于瑞文光速QA实战教程',//微课名称
          immediate:true,//是否直播
          price:100,//价格
          Single:false,//true为单课时，false为系列课
          singleNum:0,
          person:25000,//参与人数
          startTime:1519962989,//开始时间
      },{
          id:2,
          imgUrl:'https://static-1253442168.image.myqcloud.com/lecture_cover/cover_5495240_eca821dbc77706ceb0c304c225ed0c24.jpg-lecturecover85hd',//微课图片
          name:'男孩的高调唱法',//微课名称
          immediate:false,//是否直播
          price:100,//价格
          Single:true,//true为单课时，false为系列课
          singleNum:15,
          person:7962,//参与人数
          startTime:1519962989,//开始时间
      },{
          id:3,
          imgUrl:'https://static-1253442168.image.myqcloud.com/lecture_cover/cover_5501166_5a5badd1092fa0c9e8598de59176ce4d.jpg-lecturecover85hd',//微课图片
          name:'这一次，你选择做你自己',//微课名称
          immediate:true,//是否直播
          price:0,//价格
          Single:true,//true为单课时，false为系列课
          singleNum:23,
          person:5602,//参与人数
          startTime:1519962989,//开始时间
      },{
          id:4,
          imgUrl:'https://static-1253442168.image.myqcloud.com/upload/5682974_e6c837550e93071f334ff105614bd077.jpg-lecturecover85hd',//微课图片
          name:'凌晨四点半，海棠花未眠',//微课名称
          immediate:true,//是否直播
          price:100,//价格
          Single:true,//true为单课时，false为系列课
          singleNum:6,
          person:23400,//参与人数
          startTime:1519962989,//开始时间
      },{
          id:5,
          imgUrl:'https://static-1253442168.image.myqcloud.com/lecture_cover/cover_5608564_af98a3bf003008c5f289d2c519941d87.jpg-lecturecover85hd',//微课图片
          name:'哈哈哈哈哈',//微课名称
          immediate:false,//是否直播
          price:0,//价格
          Single:false,//true为单课时，false为系列课
          singleNum:0,
          person:63500,//参与人数
          startTime:1519962989,//开始时间
      },]
    }
  },
  methods:{
    listSort(){
      let immediateList=[],
          noimmeList=[];
      this.classList.map((val,index)=>{
        val.startTime=Interface.timeFormat(val.startTime);
        if(val.immediate){
          immediateList.push(val);
        }else{
          noimmeList.push(val);
        }
      })
      this.classList=immediateList.concat(noimmeList)
    },
    jump(scence,param){
      if(scence == 'detail'){
        this.$router.push('/login')
        sessionStorage.setItem('classObj',JSON.stringify(param))
      }
    }
  },
  created(){
    this.listSort();
  },
  mounted(){
     
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    @import 'formula.scss';
</style>



